@import '../modules';

// pagination ê³µí†µ ì†ì„±
.pagination{
  display: flex;
  justify-content: center;
  align-items: center;
  
  // arrow ë°©í–¥ í‚¤ ê³µí†µì†ì„±
  .btn-ar{
    width: 2rem;
    height: 2.6rem;

    // ì™¼ìª½ ë°©í–¥ì¼ ë•Œ, íšŒì „ì‹œí‚´
    &.--left{
      margin-left: 8px;
      transform: rotate(180deg);
    }
    // ì˜¤ë¥¸ìª½ ë°©í–¥ì¼ ë•Œ,
    &.--right{
      margin-right: 8px;
    }
    
    // ë²„íŠ¼ í´ë¦­ ë¶ˆê°€ëŠ¥
    &.--disable{
      pointer-events: none;
    }

    // ë²„íŠ¼ í´ë¦­ ê°€ëŠ¥
    &.--able{
      svg path{
        fill: #797979;
        transition: fill 0.3s;
      }
    }

    .svg{
      width: 2rem;
      height: 2.6rem;
    }
  }

  // pagination ìˆ«ìžë“¤
  .pagination-nums{
    display: inline-flex;
    align-items: center;
    padding: 0 16px;

    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;

      &:hover{
        text-decoration: underline;
      }
    }

    // í˜„ìž¬ íŽ˜ì´ì§€ ìˆ«ìž
    .now{
      button{
        color: $azure;
        font-weight: 700;
      }
    }
  }
}

// pagination êµ¬ë¦„ ì»¤ë®¤ë‹ˆí‹°
.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $commu_cyan_01;
  }
}


// * ë²„íŠ¼ - ëª©ë¡ë³´ê¸°
.btn-link-list{
  display: inline-flex;
  align-items: center;
  height: 5.8rem;
  padding: 0 4.4rem;
  border-radius: 4rem;

  color: #666666;
  border: 1px solid #B0B0B0;

  background-color: white;
  transition: all 0.2s;
  
  .icon{
    margin-right: 1rem;
  }

  // ë™ìž‘ì •ì˜
  &:hover{
    color: #3C3C3C;
    border: 1px solid #666666;
  }

  &:active{
    color: #212121;
    border: 1px solid #3C3C3C;
  }
}


// * ðŸ‘½ï¸responsive
@include mobile-max{
  .pagination{
    width: 100%;

    .btn-ar{
      
      .svg{
        width: 1.6rem;  
        height: 2.1rem;

      }
    }

    .pagination-nums{
      padding: 0 12px;
      column-gap: 0.2rem;
    }
  }

  .btn-link-list{
    height: 3.6rem;
    padding: 0 2.6rem;

    font-size: 1.2rem;

    .icon{
      width: 1.8rem;
      height: 1.8rem;
    }
  }

}