@import './../modules';


// subpageì— banner - basic
.subpage-banner{
  position: relative;
  padding-top: $number_nav_height-84;
  height: calc(38rem + 8.4rem);

  color: $white;
  background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);

  // ì„œë¸Œ ë©”ë‰´
  .subpage-depth2{
    position: absolute;
    width: 100%;
    display: block;
    background-color: rgba(52, 92, 224, 0.18);
    z-index: 9;

    // depth2 ë©”ë‰´ë“¤
    .depth2__container{
      display: flex;

      a{
        display: inline-block;
        padding: 2.2rem 0.5rem 2.4rem 0.5rem;
        margin: 0 1em;

        font-weight: 500;
        color: rgba(255, 255, 255, 0.6);
        @include grow_fontw(white, 0.3s);
      }
    }

  }

  // ì„œë¸Œ ë‚´ìš©
  .subpage-cont{
    position: relative;
    padding: 16rem 1rem 4.6rem 1rem;
    vertical-align: baseline;

    div{
      position: absolute;
      bottom: 0%;
    }

    h2{
      padding-bottom: 3.2rem;
      @include style_font(700, 3.8rem, 1, white);
      transform: translateX(3rem);

    }

    p{
      height: 10rem;
      transform: translateX(3rem);

      @include style_font(400, 2.4rem, 1.8em, white);
      span{display: block;}
    }

    // tmaxêµ¬ë¦„ì˜ ê²½ìš° ë‹¤ìš´ë¡œë“œ ë²„íŠ¼ì´ ìžˆìŒ
    .btn-download-tmaxgr{
      padding: 1.6rem 6.5rem ;
      position: absolute;
      bottom: 5.6rem;
      right: 20.4rem;

      width: 23rem;
      height: 6.8rem;

      &:hover{
        background-color: $white;
      }
    }
  }
}

.subpage-banner.commu{ 
  background: linear-gradient(90.72deg, #00BAE2 0.62%, #00E0D3 99.38%);
  
  .subpage-depth2 .depth2__container{
    // ! ìž„ì‹œ 
    :not(li:first-child) a{
      display: none;
    }
  }
}


