// * ðŸ¦„ï¸Interaction
.subpage-banner{

  // ì•ˆë‚´ë¬¸êµ¬
  .subpage-cont{
    h2{
      @include animation(0s, 1s, bannerContRightToLeft, ease-out);
    }
    p{
      @include animation(0s, 1s, bannerContRightToLeft, ease-out);

      // @include animation(0.1s, 1s, bannerContRightToLeft, ease-out);
    }
  }

  // ë°°ë„ˆ ì´ë¯¸ì§€
