// * ðŸ‘½ï¸responsive
@include mobile-max{
  
  .subpage-banner{
    padding-top: 4.8rem;
    height: calc(13.5rem + 9.6rem);

    .subpage-depth2{

      .depth2__container{
        a{
          font-weight: 400;
          padding: 1.7rem 0.5rem 1.9rem 0.5rem;
          font-size: 1.2rem;
        }
      }
    }

    .subpage-cont{
      padding: 8.7rem 1rem 0 2rem;

      h2{
        font-size: 1.8rem;
        padding-bottom: 1.8rem;
      }

      p{
        display: none;
        &.important{
          display: block;
          font-size: 1.2rem;
          line-height: 1.4;
        }
      }

      .btn-download-tmaxgr{
        display: none;
      }
    }

    .illust-subpage{
      width: 29.4rem;
      height: 13.5rem;
    }
  }
}