.commu{

  .board-wrap{
    text-align: center;
    background-color: white;
    border-radius: 3rem;
    box-shadow: 0px 5px 20px rgba(45, 52, 74, 0.03);

    // ê²Œì‹œíŒ ìƒë‹¨
    .title{
      display: grid;
      grid-template-columns: 91.2rem 13.6rem 15.2rem 12.4rem;
      align-items: center;
      vertical-align: middle;
      column-gap: 8px;
      justify-content: center;

      span{
      @include style_font(500, 1.6rem, 1, $font_color-797979_grey);
        padding: 1.2rem;
        height: 4rem;
        background-color: $bg_color-F8F8F8-grey;
        border-radius: 5px;
        i.icon{
          vertical-align: top;
          height: 1.6rem;
          margin-left: 6px;
        }
      }
    }

    // ê²Œì‹œíŒ ë‚´ìš©
    ul.cont-ul{
  
      li{
        display: grid;
        grid-template-columns: 91.2rem 13.6rem 15.2rem 12.4rem;
        align-items: center;
        vertical-align: middle;
        column-gap: 8px;
        justify-content: center;

        border-bottom: 1px solid #ececec;

        a{
          display: flex;
          align-items: center;
          padding: 1rem 0;
          text-align: left;

          height: 9.4rem;

          h4{
            padding-left: 2.5rem;
            padding-right: 8px;
            @include style_font(500, 2rem, 1, $font_color-212121_grey);
          }
          span.views{
            margin-right: 6px;
            color: $commu_cyan_01;
          }
          i.icon{
            width: 2.4rem;
            height: 2.4rem;
            margin: 0 2px;
          }
        }
      }
    }
  }

  // ë²„íŠ¼ - ê¸€ì“°ê¸°
  .btn-write{
    margin-top: 4rem;
    margin-left: auto;
    
    .icon{
      width: 1.6rem;  
      height: 1.8rem;
      margin-left: 0;
      margin-right: 1rem;
      background-image: none;
      svg path{
        fill: white;
      }
    }
  }

}


